// src/Carousel.js
import React, { useState, useEffect } from "react";
import slide1 from "../assets/slide1.png";
import slide2 from "../assets/slide2.png";
import slide3 from "../assets/slide3.jpg";

const images = [slide1, slide2, slide3];

const Carousel = () => {
  const [currentIndex, setCurrentIndex] = useState(0);

  // Automatically transition to the next slide
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) =>
        prevIndex === images.length - 1 ? 0 : prevIndex + 1
      );
    }, 5000); // Change slide every 5 seconds

    return () => clearInterval(interval); // Clean up the interval on component unmount
  }, []);

  const prevSlide = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? images.length - 1 : prevIndex - 1
    );
  };

  const nextSlide = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === images.length - 1 ? 0 : prevIndex + 1
    );
  };

  return (
    <div className="bg-lightPink">
      <div className="bg-lightPink relative w-full max-w-xl mx-auto">
        <div className="overflow-hidden rounded-lg">
          <img
            src={images[currentIndex]}
            alt={`Slide ${currentIndex + 1}`}
            className="w-full h-64 object-cover transition duration-1000 ease-in-out"
          />
        </div>
        <button
          onClick={prevSlide}
          className="absolute top-1/2 left-0 transform -translate-y-1/2 bg-darkpink text-white p-2 rounded-r-md focus:outline-none"
        >
          &#10094;
        </button>
        <button
          onClick={nextSlide}
          className="absolute top-1/2 right-0 transform -translate-y-1/2 bg-darkpink text-white p-2 rounded-l-md focus:outline-none"
        >
          &#10095;
        </button>
        <div className="flex justify-center mt-4">
          {images.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentIndex(index)}
              className={`w-3 h-3 mx-2 rounded-full ${
                currentIndex === index ? "bg-darkpink" : "bg-lightpink"
              }`}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Carousel;
